#top{
    width: 100%;
    height: 150px;
}

#main{
    width: 100%;
    height: 450px;

    display: flex;
    flex-direction: column;
    align-items: center;
}

#title{
    width: 300px;
    height: 20px;
    bottom: 0px;

    margin: 50px 0 50px 0;
    
    /* bigBold */
    
    font-family: 'Noto Sans CJK KR';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 149.8%;
    /* identical to box height, or 30px */
    
    text-align: center;
    letter-spacing: -0.015em;
    
    /* primary/purple */
    
    color: #555555;
}

#box{
    width: 300px;
    height: 250px;

    margin: 0 0 0 0;
    display: flex;

    box-shadow: 2px 4px 8px #888888;
    border-width: 0px;
    border-radius: 10px;
}